.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 345px;
  margin: 0 auto;
  padding: 20px 0px 40px;

}

.app-logo {
  height: 50px;
  margin-right: 10px;
}

.bar-nam-div{
  text-align: center;  
}

.bar-name {
  font-size: 1.5rem;
  color: #CCCCCC;
  align-items: center;
}

.menu-wrapper {
  position: relative;
}

.menu-icon {
  cursor: pointer;
  font-size: 2rem;
  color: #333;
}

.menu-dropdown {
  position: absolute;
  right: 0;
  top: 2rem;
  background: white;
  border: 1px solid #ddd;
  border-radius: 0.5rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  z-index: 100;
}

.menu-item {
  display: block;
  padding: 0.5rem 1rem;
  text-decoration: none;
  color: #333;
  font-weight: bold;
}

.menu-item:hover {
  background-color: #f1f1f1;
}

/* ===== */

/* Container do menu dropdown */
.menu-dropdown {
  position: absolute;
  right: 0;
  top: 2rem;
  background: #1a1a1a;
  border: 2px solid #31e895;
  border-radius: 10px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
  z-index: 100;
  min-width: 180px;
  overflow: hidden;
}

/* Estilo dos itens do menu */
.menu-item {
  display: block;
  padding: 15px 20px;
  margin: 0;
  text-decoration: none;
  color: #FFFFFF;
  font-weight: 600;
  font-size: 15px;
  transition: all 0.3s ease;
  border-bottom: 1px solid rgba(49, 232, 149, 0.2);
}

/* Animação de entrada do menu */
.menu-dropdown {
  animation: slideDown 0.2s ease-out;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsividade */
@media (max-width: 480px) {
  .menu-dropdown {
    right: -10px;
    min-width: 160px;
  }
  
  .menu-item {
    padding: 12px 16px;
    font-size: 14px;
  }
}